import { userInfo } from "server/api";


export const setUserInfo = (data) => ({
    type: 'SET_USERINFO',
    data
})

// 
export const getUserInfo = () => async dispatch => {
    const res = await userInfo()
    if (res.status == "success") {
        dispatch(setUserInfo(res.data));
    }
    return Promise.resolve(res)
}